<template>
    <div class='user'>
        <div class="header">
            <h2><img :src="baseUrl + user.icon" alt=""/></h2>
            <div class="user-box">
                <a>{{user.nikename}}</a>
                <a href="javaScript:;" @click="logout"></a>
            </div>
            </div>
            <div class="docList">
            <ul>
                <li class="gk-text">
                    <i></i>
                    <p></p>
                    <b></b>
                    <span>0</span>
                </li>
                <li class="mm-text">
                    <i></i>
                    <p></p>
                    <b></b>
                    <span>0</span>
                </li>
                <li class="cg-text">
                    <i></i>
                    <p></p>
                    <b></b>
                    <span>0</span>
                </li>
                <li class="sc-text">
                    <i></i>
                    <p></p>
                    <b></b>
                    <span></span>
                </li>
                    <li class="my_cz">
                    <i></i>
                    <p></p>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>

import router from '../plugins/router'

export default {
    data(){
        return {
            user:{}
        }
    },
    beforRouterEnter(to,from,next){
        //
        axios({
            url:'/api/user'
        }).then(
            res=>{
                if(res.data.err===0){
                    next(_this=>_this.user = res.data.data)
                }else{
                    router.push('/login')
                    next('/login')
                }
            }
        )
    },
    methods:{
        logout(){
            axios({
                url:'/api/logout',
                method:'delete'
            }).then(
                res=>res.data.err === 0 && this.$router.push('/login')
            )
        }
    }
}
</script>

<style scoped>
  @import url('../assets/css/user.css');/* */
</style>