<template>
    <div class='home'>
        <Banner :banner="banner"></Banner>
        <List :list="list" dataName="list" ></List>
    </div>
</template>
<script>

import Banner from '@/components/Banner'
import List from '@/components/List'

export default {
    name:'list',
    data(){
      return {
        list:[],
        banner:[]
      }
    },
    components:{Banner,List},
    mounted(){

      axios({
        url:'/api/list'
        // params:{_page:1,_limit:10}
      }).then(
        // console.log(1),
        // res=>console.log(res.data),
        res=>this.list = res.data.data
      )
      axios({
      url:'/api/banner',
      // params:{_page:1,_limit:4}
    }).then(
      res=>{
        // console.log(res)
        this.banner = res.data.data;
      }
    )
    }
  }
</script>
