<template>
    <div class='con'>
        <p class="fhbtn"><a href="javascript:;" onclick="window.history.go(-1)"></a></p>
    <!-- <h1><img src="../../public/images/touxiang.jpeg" alt=""></h1> -->
    <h1></h1>
    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
        <router-link to="/login"></router-link>
        <router-link to="/reg"></router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
      <li class="lifirst">
           <span></span><input type="text" v-model="username"/>
      </li>
      <li>
          <span></span><input type="text" v-model="password"/>
      </li>
      <p>
        {{mess}}
      </p>
    </ul>
    <div class="footbox">
      <input type="button" value=" " class="login-btn" @click="login"/>
      <a href="javascript:;" class="tishi"></a>
    </div>
    </div>
</template>
<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      mess:''
    }
  },
  methods:{
    login(){
      axios({
        url:'/api/login',
        method:'post',
        data:{username:this.username,password:this.password}
      }).then(
        res => {
          console.log('1',res);
          if(res.data.err===0){
            this.$router.push('/user')
          }else{
            this.mess = res.data.msg
          }
        }
      )
    }
  }
}
</script>

<style scoped>
.con{max-width:6.4rem; margin:0 auto;}
.con .fhbtn a{ display:block;width:100%;height:0.6rem;background:tomato;text-indent: 0.2rem;font:0.26rem/0.6rem "";}
.con h1{width:2.18rem;height:1.5rem; margin-top:0.8rem;background:url(../assets/img/touxiang.png) no-repeat 0 0;background-size:100%; margin:0 auto;margin-top:1.22rem;}
/* .con h1 img{width:2.2rem;height:2.2rem;border-radius: 50%;margin-left:33%;} */
.con .login{width:2.0rem;height:0.38rem; margin:0 auto; margin-top:0.85rem;}
.login a{width:0.97rem;height:0.18rem; font-size:0.35rem; line-height:0.38rem; float:left; color:#4c4f50; text-align:center;}
.login span{ float:left;height:0.38rem; border-left:1px solid #4c4f50;}
.con .login-box{ position:relative;}
.con .login-box .lsolid{width:1.96rem;height:0.18rem; border-bottom:1px solid #4c4f50; position:absolute;top:0;left:0.1rem;}
.con .login-box .rsolid{width:1.96rem;height:0.18rem; border-bottom:1px solid #4c4f50; position:absolute;top:0;right:0.1rem;}

.con ul{background:#fff; margin:0.5rem 0 0 0.4rem;;}
.con ul li{height:0.75rem;}
.con ul li input{width:4.54rem;height:0.5rem;; font-size:0.4rem;border: 0.02rem solid #ccc;border-left:0;}
.con ul li span{float:left;display:block;width:1rem;height:0.54rem;text-align:center;background:tomato;font:0.24rem/0.54rem "";}

.con .footbox{width:4.65rem;height:0.65rem;margin:0 auto; }
.con .login-btn{width:4.65rem;height:0.65rem; background:orangered; color:#fff; text-align:center; line-height:0.65rem; border-radius:8px; display:block; margin-top:0.62rem; font-size:0.28rem;}
.con .tishi{width:1.4rem; font-size:0.25rem; margin-top:0.28rem; display:block;}
</style>

