<template>
    <div class='reg'>
        <p class="fhbtn"><a href="javascript:;" onclick="window.history.go(-1)"></a></p>
    <!-- <h1><img src="../../public/images/reg.jpg" alt=""></h1> -->
    <input type="file" ref="f1">

    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
        <router-link to="/login"></router-link>
        <router-link to="/reg"></router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
      <li class="lifirst">
           <span></span><input type="text" v-model="username">
      </li>
      <li>
          <span></span><input type="text" v-model="password"/>
      </li>
    </ul>
    <div class="footbox">
      <input type="button" value=" " class="login-btn" @click="reg"/>
    </div>
    </div>
</template>
<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      mess:''
    }
  },
  methods:{
    reg(){
      let formData = new FormData();
      formData.append('username',this.username)
      formData.append('password',this.password)
      // formData.append('nikename','')
      formData.append('icon',this.$refs.f1.files[0])

      axios({
        url:'/api/reg',
        method:'post',
        data:formData
      }).then(
        // res=>console.log(res.data),
        res => res.data.err===0 ? this.$router.push('/login') : this.mess = res.data.msg
      )
    }
  }
}
</script>

<style scoped>
.reg{max-width:6.4rem; margin:0 auto;}
.reg .fhbtn a{ display:block;width:100%;height:0.6rem;background:tomato;text-indent: 0.2rem;font:0.26rem/0.6rem "";}
.reg h1{width:100%; margin-top:0.8rem;}
.reg h1 img{width:2.2rem;height:2.2rem;border-radius: 50%;margin-left:33%;}
.reg .login{width:2.0rem;height:0.38rem; margin:0 auto; margin-top:0.85rem;}
.login a{width:0.97rem;height:0.18rem; font-size:0.35rem; line-height:0.38rem; float:left; color:#4c4f50; text-align:center;}
.login span{ float:left;height:0.38rem; border-left:1px solid #4c4f50;}
.reg .login-box{ position:relative;}
.reg .login-box .lsolid{width:1.96rem;height:0.18rem; border-bottom:1px solid #4c4f50; position:absolute;top:0;left:0.1rem;}
.reg .login-box .rsolid{width:1.96rem;height:0.18rem; border-bottom:1px solid #4c4f50; position:absolute;top:0;right:0.1rem;}

.reg ul{background:#fff; margin:0.5rem 0 0 0.4rem;;}
.reg ul li{height:0.95rem;}
.reg ul li input{width:4.54rem;height:0.5rem;; font-size:0.4rem;border: 0.02rem solid #ccc;border-left:0;}
.reg ul li span{float:left;display:block;width:1rem;height:0.54rem;text-align:center;background:tomato;font:0.24rem/0.54rem "";}

.reg .footbox{width:4.65rem;height:0.65rem;margin:0 auto; }
.reg .login-btn{width:4.65rem;height:0.65rem; background:orangered; color:#fff; text-align:center; line-height:0.65rem; border-radius:8px; display:block; margin-top:0.62rem; font-size:0.28rem;}
</style>

